import { getImages } from "./get-images-api.js";
const galleryButtonEl = document.querySelector(".gallery-button");

let currentPage = 1;

const renderImages = (images) => {
    const galleryEl = document.querySelector(".gallery");

    galleryEl.innerHTML = "";

    images.forEach((image) =>{
        const galleryItemEl = document.createElement("li");

        galleryItemEl.innerHTML = `
            <img class="gallery-image" src="${image.webformatURL}" alt="${image.tags}"></img>
        `;

        galleryEl.append(galleryItemEl);
    });
}

galleryButtonEl.addEventListener("click", () => {
    getImages(currentPage).then((images) => {
        renderImages(images);
        currentPage += 1;
    });
});

getImages(1).then((images) => {
    renderImages(images);
});

