const API_KEY = "48225405-5dd56fa809ed0a2375f53541a";

const pageSize = 12;

export const getImages = (page) => {
    const API = `https://api.allorigins.win/get?url=https://pixabay.com/api?key=${API_KEY}&q=car&image_type=photo&page=${page}&per_page=${pageSize}`;
    return fetch(API).then((res) => res.json()).then((data) => {
        console.log(JSON.parse(data.contents).hits);
        return JSON.parse(data.contents).hits;
    });
}